          </div>
          
          <!-- 错误提示 -->
          <div v-if="cameraError" class="camera-error">
            <van-icon name="warning-circle" color="#f53f3f" size="24" />
            <p>{{ cameraError }}</p>
          </div>
        </div>
        
        <div class="camera-controls">
          <van-button 
            type="primary" 
            round 
