            :disabled="!isCameraActive || isProcessingImage"
          >
            <van-icon name="camera" size="18" style="margin-right: 5px;" />
            {{ isProcessingImage ? '处理中...' : '拍照分析' }}
          </van-button>
          
          <van-button 
            type="info" 
            round 
            @click="startContinuousAnalysis"
            :disabled="!isCameraActive || isAnalyzingContinuously"
          >
            <van-icon name="play" size="18" style="margin-right: 5px;" />
            {{ isAnalyzingContinuously ? '停止连续分析' : '连续分析' }}
          </van-button>
        </div>
      </div>
    </van-popup>
    
    <!-- 评测报告弹窗 -->
    <van-popup
      v-model:show="isReportVisible"
      position="center"
      :style="{ width: '90%', maxWidth: '800px', height: '90%', overflow: 'auto' }"
    >
      <div class="report-container">
        <div class="report-header">
          <h2>面试多维度评估报告</h2>
          <van-icon name="close" @click="isReportVisible = false" class="close-icon" />
        </div>
        
        <div v-if="reportLoading" class="report-loading">
          <van-loading type="spinner" color="#1677ff" size="30" />
          <p>正在生成评估报告...</p>
        </div>
        
        <div v-else-if="reportError" class="report-error">
          <van-icon name="warning-circle" color="#f53f3f" size="24" />
          <p>{{ reportError }}</p>
          <van-button @click="generateReportFromStorage" type="primary" round>重试</van-button>
        </div>
        
        <div v-else class="report-content">
          <!-- 雷达图区域 -->
          <div class="radar-chart-container">
            <h3 class="chart-title">多维度能力雷达图</h3>
            <div class="chart-wrapper">
              <canvas ref="radarChart" width="400" height="300"></canvas>
            </div>
          </div>
          
          <!-- 能力详情区域 -->
          <div class="ability-details-container">
            <h3 class="details-title">各维度评分详情</h3>
            <div class="details-list">
              <div v-for="(item, index) in reportIndicators" :key="index" class="detail-item">
                <span class="indicator-name">{{ item }}</span>
                <span class="score-value" :class="getScoreClass(reportValues[index])">
                  {{ reportValues[index] }}分 ({{ getScoreLevel(reportValues[index]) }})
                </span>
              </div>
            </div>
          </div>
          
          <!-- 详细分析区域 -->
          <div class="analysis-container">
            <h3 class="analysis-title">详细分析</h3>
            
            <div class="analysis-section">
              <h4>专业知识水平</h4>
              <p>{{ professionalKnowledgeAnalysis || '暂无数据' }}</p>
              <p v-if="firstQuestion"><strong>问题:</strong> {{ firstQuestion }}</p>
              <p v-if="firstResult"><strong>回答:</strong> {{ firstResult }}</p>
            </div>
            
            <div class="analysis-section">
              <h4>技能匹配度</h4>
              <p>{{ skillMatchAnalysis || '暂无数据' }}</p>
              <p v-if="secondQuestion"><strong>问题:</strong> {{ secondQuestion }}</p>
              <p v-if="secondResult"><strong>回答:</strong> {{ secondResult }}</p>
            </div>
            
            <div class="analysis-section">
              <h4>语言表达能力</h4>
              <p>{{ languageExpressionAnalysis || '暂无数据' }}</p>
              <p v-if="thirdQuestion"><strong>问题:</strong> {{ thirdQuestion }}</p>
              <p v-if="thirdResult"><strong>回答:</strong> {{ thirdResult }}</p>
            </div>
            
            <div class="analysis-section">
              <h4>逻辑思维能力</h4>
              <p>{{ logicalThinkingAnalysis || '暂无数据' }}</p>
              <p v-if="fourthQuestion"><strong>问题:</strong> {{ fourthQuestion }}</p>
              <p v-if="fourthResult"><strong>回答:</strong> {{ fourthResult }}</p>
            </div>
            
            <div class="analysis-section">
              <h4>创新能力</h4>
              <p>{{ innovationAbilityAnalysis || '暂无数据' }}</p>
              <p v-if="fifthQuestion"><strong>问题:</strong> {{ fifthQuestion }}</p>
              <p v-if="fifthResult"><strong>回答:</strong> {{ fifthResult }}</p>
            </div>
          </div>
          
          <!-- 关键问题定位 -->
          <div class="analysis-container">
            <h3 class="analysis-title">关键问题定位</h3>
            <div class="issue-location">
              <p>{{ keyIssues || '暂无关键问题定位' }}</p>
              
              <div v-if="issueExamples && issueExamples.length">
                <h4>具体表现示例:</h4>
                <ul>
                  <li v-for="(example, index) in issueExamples" :key="index">
                    {{ example }}
                  </li>
                </ul>
              </div>
            </div>
          </div>

          <!-- 反馈建议 -->
          <div class="analysis-container">
            <h3 class="analysis-title">反馈建议</h3>
            <div class="feedback-suggestions">
              <div v-if="feedbackCategories && feedbackCategories.length">
                <div v-for="(category, catIndex) in feedbackCategories" :key="catIndex" class="feedback-category">
                  <h4>{{ category.title }}</h4>
                  <ul>
                    <li v-for="(suggestion, sugIndex) in category.suggestions" :key="sugIndex">
                      {{ suggestion }}
                    </li>
                  </ul>
                </div>
              </div>
              <p v-else>暂无具体反馈建议</p>
            </div>
          </div>
          
          <!-- 综合评价 -->
          <div class="summary-container">
            <h3 class="summary-title">综合评价与建议</h3>
            <div class="summary-content">
              <p>{{ comprehensiveEvaluation || '暂无综合评价' }}</p>
              
              <h4>改进建议</h4>
              <ul v-if="improvementSuggestions.length">
                <li v-for="(suggestion, index) in improvementSuggestions" :key="index">
                  {{ suggestion }}
                </li>
              </ul>
              <p v-else>暂无建议</p>
            </div>
          </div>
          
          <!-- 响应体展示区域 -->
          <div class="response-body-container">
            <h3 class="response-body-title">原始响应数据</h3>
            <div class="response-body-content">
              <pre>{{ formattedReportResponse }}</pre>
            </div>
          </div>
        </div>
      </div>
    </van-popup>
    
    <!-- 录音提示 -->
    <div v-if="isRecording" class="recording-indicator">
      <div class="recording-dot"></div>
      <p>正在录音...{{ recordingSeconds }}s</p>
      <p>松开结束录音</p>
    </div>
    
    <!-- 服务状态提示 -->
    <div 
      class="status-bar" 
      :class="statusClass"
      style="z-index: 998;"
    >
      {{ statusText }}
    </div>
    
    <!-- 语音合成状态提示 -->
    <div v-if="ttsStatusMessage" :class="ttsStatusClass" class="tts-status-message">
      {{ ttsStatusMessage }}
    </div>
  </div>
</template>

<script>
import { Chart, registerables, RadarController } from 'chart.js';
import axios from 'axios';

export default {
  data() {
    return {
      // 关键问题定位
      keyIssues: '',
      issueExamples: [],
      
      // 反馈建议
      feedbackCategories: [],
      
      name: this.$route.params.name,
      message: "",
      messages: [],
      isTyping: false,
      typingId: "",
      API_BASE_URL: 'https://123.56.203.202',
      TTS_API_URL: 'https://123.56.203.202/gpt-sovites/tts', // 语音合成API地址
      statusText: '连接中...',
      statusClass: 'text-xs text-gray-500 mt-2 text-center',
      commonHeaders: {
        'ngrok-skip-browser-warning': '1'
      },
      
      // 语音合成相关状态
      ttsAudioElements: {}, // 存储文字转语音的音频元素
      ttsStatusMessage: '', // 语音合成状态消息
      ttsStatusClass: '', // 语音合成状态样式
      
      // 语音相关状态
      isRecording: false,
      mediaRecorder: null,
      audioBlob: null,
      recordingSeconds: 0,
      recordingTimer: null,
      audioContext: null,
      audioElements: {},
      mediaStream: null,
      audioChunks: [],
      transcribeCount: 0, // 转写计数，用于跟踪是第几次转写
      
      // 相机相关状态
      isCameraModalVisible: false,
      isCameraActive: false,
      isLoadingCamera: false,
      cameraError: null,
      isProcessingImage: false,
      isAnalyzingContinuously: false,
      continuousAnalysisTimer: null,
      detectionInterval: null,
      videoStream: null,
      
      // 评测报告相关状态
      isReportVisible: false,
      reportLoading: false,
      reportError: '',
      chart: null,
      reportResponse: null, // 存储原始响应数据
      
      // 报告数据 - 五个维度
      reportIndicators: [
        '专业知识水平',
        '技能匹配度',
        '语言表达能力',
        '逻辑思维能力',
        '创新能力'
      ],
      reportValues: [60, 60, 60, 60, 60],
      
      // 问题和答案存储
      firstQuestion: '',
      secondQuestion: '',
      thirdQuestion: '',
      fourthQuestion: '',
      fifthQuestion: '',
      firstResult: '',
      secondResult: '',
      thirdResult: '',
      fourthResult: '',
      fifthResult: '',
      
      // 详细分析数据
      professionalKnowledgeAnalysis: '', // 专业知识水平
      skillMatchAnalysis: '', // 技能匹配度
      languageExpressionAnalysis: '', // 语言表达能力
      logicalThinkingAnalysis: '', // 逻辑思维能力
      innovationAbilityAnalysis: '', // 创新能力
      comprehensiveEvaluation: '',
      improvementSuggestions: []
    };
  },
  computed: {
    // 格式化响应体数据用于展示
    formattedReportResponse() {
      if (!this.reportResponse) return '无响应数据';
      return JSON.stringify(this.reportResponse, null, 2);
    }
  },
  mounted() {
    this.testConnection();
    this.initContainerHeight();
    window.addEventListener('resize', this.initContainerHeight);
    this.checkRecordingSupport();
    
    // 从localStorage加载已保存的问题和答案
    this.loadStoredQuestionsAndAnswers();
    
    // 注册Chart.js组件
    Chart.register(...registerables);
    Chart.register(RadarController);
    window.Chart = Chart;
    
    // 启动面试流程
    this.startInterviewProcess();
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.initContainerHeight);
    this.stopRecording();
    
    // 停止所有语音播放
    Object.values(this.audioElements).forEach(audio => {
      if (audio) {
        audio.pause();
      }
    });
    
    // 停止所有文字转语音播放
    Object.values(this.ttsAudioElements).forEach(audio => {
      if (audio) {
        audio.pause();
      }
    });
    
    // 清理相机相关资源
    this.stopCamera();
    if (this.continuousAnalysisTimer) {
      clearInterval(this.continuousAnalysisTimer);
    }
    if (this.detectionInterval) {
      clearInterval(this.detectionInterval);
    }
    
    // 销毁图表
    if (this.chart) {
      this.chart.destroy();
    }
  },
  methods: {
    // 从localStorage加载已保存的问题和答案
    loadStoredQuestionsAndAnswers() {
      this.firstQuestion = localStorage.getItem("firstQuestion") || '';
      this.secondQuestion = localStorage.getItem("secondQuestion") || '';
      this.thirdQuestion = localStorage.getItem("thirdQuestion") || '';
      this.fourthQuestion = localStorage.getItem("fourthQuestion") || '';
      this.fifthQuestion = localStorage.getItem("fifthQuestion") || '';
      
      this.firstResult = localStorage.getItem("firstResult") || '';
      this.secondResult = localStorage.getItem("secondResult") || '';
      this.thirdResult = localStorage.getItem("thirdResult") || '';
      this.fourthResult = localStorage.getItem("fourthResult") || '';
      this.fifthResult = localStorage.getItem("fifthResult") || '';
    },
    
    // 开始面试流程
    startInterviewProcess() {
      // 模拟avatarPlatform2已实例化
      const avatarPlatform2 = true;
      
      if (avatarPlatform2) {
        // 初始调用 - 问候语
        let text = `你好，${localStorage.getItem("username") || "候选人"}，欢迎面试${localStorage.getItem("career") || "相关岗位"}。接下来将进行面试环节，共有五道题目，涉及专业知识水平、技能匹配度、语言表达能力、逻辑思维能力和创新能力五个维度。每道题目间隔60秒，请认真思考后回答。`;

        this.addMessage('ai', text, 'text');
        
        // 设置30秒后自动发送第一道题
        setTimeout(() => {
          let career = localStorage.getItem("career");
          text = `请详细阐述您对${career || '该'}领域中核心概念和技术的理解，以及您在实际项目中如何应用这些知识解决问题。`;
          // 使用追加方式保存到localStorage
          if (!localStorage.getItem("firstQuestion")) {
            localStorage.setItem("firstQuestion", text);
          }
          this.firstQuestion = text;
          this.addMessage('ai', text, 'text');
