    // 发送文本消息
    async sendMessage() {
      const prompt = this.message.trim();
      if (!prompt) return;
      
      this.addMessage('user', prompt, 'text');
      this.message = '';
      this.scrollToBottom();
      
      const typingId = this.addTypingIndicator();
      
      try {
        fetch(`${this.API_BASE_URL}/api/chat`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ prompt: prompt })
        }).then(response => {
          this.removeTypingIndicator(typingId);
          
          if (!response.ok) {
            throw new Error(`请求失败: ${response.status}`);
          }
          
          return response.json();
        }).then(data => {
          if (data.status === 'success') {
            this.addMessage('ai', data.result, 'text');
            // 消息添加完成后自动触发语音合成（新增代码）
        this.$nextTick(() => {
          // 获取最新添加的AI消息索引
          const lastIndex = this.messages.length - 1;
          // 自动调用语音合成
          this.toggleTextToSpeech(lastIndex);
          })
          } else {
            this.addMessage('ai', `错误: ${data.result}`, 'text', true);
          }
          this.scrollToBottom();
        }).catch(error => {
          this.removeTypingIndicator(typingId);
          this.addMessage('ai', `请求失败: ${error.message}`, 'text', true);
          this.scrollToBottom();
          console.error('发送消息失败:', error);
        });
      } catch (error) {
        this.removeTypingIndicator(typingId);
        this.addMessage('ai', `请求失败: ${error.message}`, 'text', true);
        this.scrollToBottom();
        console.error('发送消息失败:', error);
      }
