    // 格式化时长显示
    formatDuration(seconds) {
      if (seconds < 60) {
        return `${seconds}s`;
      } else {
        const mins = Math.floor(seconds / 60);
        const secs = seconds % 60;
        return `${mins}:${secs < 10 ? '0' + secs : secs}`;
      }
    },
    
    // 显示状态消息
    showStatus(text, type = 'info') {
      this.statusText = text;
      switch(type) {
        case 'success':
          this.statusClass = 'text-xs text-green-600 mt-2 text-center';
          break;
        case 'error':
          this.statusClass = 'text-xs text-red-600 mt-2 text-center';
          break;
        case 'warning':
          this.statusClass = 'text-xs text-yellow-600 mt-2 text-center';
          break;
        default:
          this.statusClass = 'text-xs text-blue-600 mt-2 text-center';
      }
      
      if (type !== 'error') {
        setTimeout(() => {
          this.statusText = '';
        }, 3000);
      }
    },
    
    // 显示语音合成状态消息
    showTTSStatus(text, type = 'info') {
      this.ttsStatusMessage = text;
      switch(type) {
        case 'success':
          this.ttsStatusClass = 'text-xs text-green-600 mt-2 text-center';
          break;
        case 'error':
          this.ttsStatusClass = 'text-xs text-red-600 mt-2 text-center';
          break;
        default:
          this.ttsStatusClass = 'text-xs text-blue-600 mt-2 text-center';
      }
      
      if (type !== 'error') {
        setTimeout(() => {
          this.ttsStatusMessage = '';
        }, 3000);
      }
    },
    
    // 文本转语音功能 - 播放或暂停
    async toggleTextToSpeech(index) {
      const msg = this.messages[index];
      if (!msg || msg.type !== 'text' || msg.sender !== 'ai') return;
      
      // 如果正在播放，则暂停
      if (msg.isPlayingTTS) {
        const audio = this.ttsAudioElements[index];
        if (audio) {
          audio.pause();
        }
        msg.isPlayingTTS = false;
        return;
      }
      
      // 停止所有其他音频播放
      this.stopAllAudioPlayback();
      
      // 检查是否已经有合成好的音频
      if (this.ttsAudioElements[index]) {
        const audio = this.ttsAudioElements[index];
        audio.play()
          .then(() => {
            msg.isPlayingTTS = true;
            audio.onended = () => {
              msg.isPlayingTTS = false;
            };
          })
          .catch(error => {
            console.error('播放失败:', error);
            this.showTTSStatus('播放失败，请重试', 'error');
          });
        return;
      }
      
      // 否则进行语音合成
      try {
        // 文本预处理：移除</think>和<|Function
    let cleanText = msg.content
      .replace(/<think>[\s\S]*?<|FunctionCallEnd|>/g, '')       // 移除</think>...<|FunctionCallEnd|>块
      .replace(/[a-zA-Z]+\/[a-zA-Z]+\s*/g, '')  // 移除字母/字母格式的标记
      .replace(/\n+/g, ' ')                 // 将换行符替换为空格
      .replace(/\s+/g, ' ')                 // 合并多个空格为一个
      .trim();                              // 去除首尾空格
        this.showTTSStatus('正在合成语音...', 'info');
        
        // 发送文本到后端进行语音合成
        const response = await axios.post(
          this.TTS_API_URL,
          { 
            text: cleanText,
            reference_text: cleanText // 使用消息内容作为参考文本
          },
          { 
            responseType: 'blob',
            headers: {
              'Content-Type': 'application/json'
            },
            timeout: 500000,
          }
        );
        
        // 将二进制音频数据转换为可播放的URL
        const blob = new Blob([response.data], { 
          type: response.headers['content-type'] || 'audio/wav' 
        });
        const audioUrl = URL.createObjectURL(blob);
        
        // 创建音频元素并播放
        const audio = new Audio(audioUrl);
        this.ttsAudioElements[index] = audio;
        
        audio.play()
          .then(() => {
            msg.isPlayingTTS = true;
            this.showTTSStatus('语音合成成功', 'success');
            
            audio.onended = () => {
              msg.isPlayingTTS = false;
            };
          })
          .catch(error => {
            console.error('播放失败:', error);
            this.showTTSStatus('播放失败: ' + error.message, 'error');
          });
          
      } catch (error) {
        console.error('语音合成失败:', error);
        
        // 处理不同类型的错误
        let errorMsg = '语音合成失败，请重试';
        if (error.response) {
          if (error.response.data?.message) {
            errorMsg = error.response.data.message;
          } else if (error.response.status === 503) {
            errorMsg = '无法连接到语音合成服务，请确保后端服务已启动';
          } else if (error.response.status === 504) {
            errorMsg = '请求超时，请尝试较短的文本';
          } else if (error.response.status === 404) {
            errorMsg = '接口不存在，请检查后端是否正确配置了路由';
          }
        } else if (error.message.includes('Network Error')) {
          errorMsg = '网络错误，请检查语音合成服务是否在运行';
        }
        
        this.showTTSStatus(errorMsg, 'error');
      }
    },
    
    // 音频格式转换相关方法
    async convertToValidWav(blob) {
      if (await this.isValidWavFile(blob)) {
        return blob;
      }
      
      try {
        const arrayBuffer = await new Response(blob).arrayBuffer();
        const audioContext = new (window.AudioContext || window.webkitAudioContext)({
          sampleRate: 16000
        });
        
        const audioBuffer = await audioContext.decodeAudioData(arrayBuffer);
        const wavBuffer = this.convertToWav(audioBuffer);
        
        return new Blob([wavBuffer], { type: 'audio/wav' });
      } catch (error) {
        console.error('音频格式转换失败:', error);
        this.showStatus('音频格式转换失败，可能影响识别效果', 'warning');
        return blob;
      }
    },
    
    async isValidWavFile(blob) {
      return new Promise((resolve) => {
        const reader = new FileReader();
        reader.onloadend = (e) => {
          if (e.target.readyState === FileReader.DONE) {
            const arrayBuffer = e.target.result;
            const uint8Array = new Uint8Array(arrayBuffer);
            
            if (uint8Array.length >= 4) {
              const riff = String.fromCharCode(uint8Array[0], uint8Array[1], uint8Array[2], uint8Array[3]);
              resolve(riff === 'RIFF');
            } else {
              resolve(false);
            }
          }
        };
        reader.readAsArrayBuffer(blob.slice(0, 4));
      });
    },
    
    convertToWav(audioBuffer) {
      const numberOfChannels = audioBuffer.numberOfChannels;
      const sampleRate = audioBuffer.sampleRate;
      const format = 1;
      const bitDepth = 16;
      
      let data = new Float32Array(audioBuffer.length);
      for (let i = 0; i < audioBuffer.length; i++) {
        let sum = 0;
        for (let c = 0; c < numberOfChannels; c++) {
          sum += audioBuffer.getChannelData(c)[i];
        }
        data[i] = sum / numberOfChannels;
      }
      
      const buffer = new ArrayBuffer(44 + data.length * 2);
      const view = new DataView(buffer);
      
      this.writeString(view, 0, 'RIFF');
      view.setUint32(4, 36 + data.length * 2, true);
      this.writeString(view, 8, 'WAVE');
      this.writeString(view, 12, 'fmt ');
      view.setUint32(16, 16, true);
      view.setUint16(20, format, true);
      view.setUint16(22, 1, true);
      view.setUint32(24, sampleRate, true);
      view.setUint32(28, sampleRate * 2, true);
      view.setUint16(32, 2, true);
      view.setUint16(34, bitDepth, true);
      this.writeString(view, 36, 'data');
      view.setUint32(40, data.length * 2, true);
      
      let index = 44;
      const volume = 1;
      for (let i = 0; i < data.length; i++) {
        const sample = Math.max(-1, Math.min(1, data[i] * volume));
        view.setInt16(index, sample < 0 ? sample * 0x8000 : sample * 0x7FFF, true);
        index += 2;
      }
      
      return buffer;
    },
    
    writeString(view, offset, string) {
      for (let i = 0; i < string.length; i++) {
        view.setUint8(offset + i, string.charCodeAt(i));
      }
    },
    
    // 相机相关方法
    async toggleCamera() {
      if (this.isCameraActive) {
        this.stopCamera();
        this.isCameraModalVisible = false;
      } else {
        this.isCameraModalVisible = true;
        await this.startCamera();
      }
    },
    
    async startCamera() {
      this.isLoadingCamera = true;
      this.cameraError = null;
      
      try {
        if (!navigator.mediaDevices || !navigator.mediaDevices.getUserMedia) {
          throw new Error('您的浏览器不支持相机功能，请使用最新浏览器');
        }
        
        if (window.location.protocol !== 'https:' && window.location.hostname !== 'localhost') {
          throw new Error('相机功能需要在HTTPS环境下运行');
        }
        
        // 相机配置
        this.videoStream = await navigator.mediaDevices.getUserMedia({
          video: { 
            width: { ideal: 1280 }, 
            height: { ideal: 720 } 
          }
        });
        
        const videoElement = this.$refs.videoElement;
        if (videoElement) {
          videoElement.srcObject = this.videoStream;
          this.isCameraActive = true;
          this.showStatus('相机已启动，可进行表情和姿态识别', 'success');
        }
      } catch (error) {
        console.error('相机启动失败:', error);
        this.cameraError = error.message;
        
        // 错误处理
        if (error.name === 'NotAllowedError') {
          this.cameraError = '相机权限被拒绝，请在浏览器设置中允许相机访问';
        } else if (error.name === 'NotFoundError') {
          this.cameraError = '未找到相机设备';
        } else if (error.name === 'NotReadableError') {
          this.cameraError = '相机被占用或不可用';
        } else if (error.name === 'NotSupportedError') {
          this.cameraError = '您的浏览器不支持相机功能';
        }
        
        this.showStatus(`相机错误: ${this.cameraError}`, 'error');
      } finally {
        this.isLoadingCamera = false;
      }
    },
    
    stopCamera() {
      if (this.videoStream) {
        this.videoStream.getTracks().forEach(track => track.stop());
        this.videoStream = null;
      }
      
      const videoElement = this.$refs.videoElement;
      if (videoElement) {
        videoElement.srcObject = null;
      }
      
      this.isCameraActive = false;
      this.isAnalyzingContinuously = false;
      
      if (this.continuousAnalysisTimer) {
        clearInterval(this.continuousAnalysisTimer);
        this.continuousAnalysisTimer = null;
      }
      
      if (this.detectionInterval) {
        clearInterval(this.detectionInterval);
        this.detectionInterval = null;
      }
      
      this.showStatus('相机已关闭', 'info');
    },
    
    // 拍照并同时分析表情和姿态
    async captureImage() {
      if (!this.isCameraActive || this.isProcessingImage) return;
      
      this.isProcessingImage = true;
      this.showStatus('正在同时分析表情和姿态...', 'info');
      
      try {
        const videoElement = this.$refs.videoElement;
        if (!videoElement) throw new Error('未找到视频元素');
        
        // 创建画布并捕获当前视频帧
        const canvas = document.createElement('canvas');
        canvas.width = videoElement.videoWidth;
        canvas.height = videoElement.videoHeight;
        const ctx = canvas.getContext('2d');
        ctx.drawImage(videoElement, 0, 0, canvas.width, canvas.height);
        
        // 转换为base64格式
        const base64Image = canvas.toDataURL('image/jpeg');
        
        // 并行获取表情分析和姿态分析结果
        const [expressionResults, poseResult] = await Promise.all([
          this.analyzeExpression(base64Image),
          this.analyzePose(base64Image)
        ]);
        
        // 创建图片URL用于显示
        const imageUrl = URL.createObjectURL(await fetch(base64Image).then(res => res.blob()));
        
        // 添加到消息列表
        this.addExpressionMessage(imageUrl, expressionResults, poseResult);
        
        this.showStatus('表情和姿态分析完成', 'success');
      } catch (error) {
        console.error('图像分析失败:', error);
        this.showStatus(`分析失败: ${error.message}`, 'error');
      } finally {
        this.isProcessingImage = false;
      }
    },
    
    // 开始/停止连续分析
    startContinuousAnalysis() {
      if (this.isAnalyzingContinuously) {
        // 停止连续分析
        clearInterval(this.continuousAnalysisTimer);
        this.continuousAnalysisTimer = null;
        this.isAnalyzingContinuously = false;
        this.showStatus('已停止连续分析', 'info');
      } else {
        // 开始连续分析，每2秒一次
        this.showStatus('开始连续分析...', 'info');
        this.isAnalyzingContinuously = true;
        
        // 立即执行一次
        this.captureImage();
        
        // 设置定时器
        this.continuousAnalysisTimer = setInterval(() => {
          if (this.isAnalyzingContinuously) {
            this.captureImage();
          }
        }, 2000);
      }
    },
    
    // 分析表情
    async analyzeExpression(base64Image) {
      try {
        const response = await fetch(`${this.API_BASE_URL}/predict/image`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ base64: base64Image })
        });
        
        if (!response.ok) {
          const errorText = await response.text();
          throw new Error(`表情分析失败: ${response.status} - ${errorText.substring(0, 100)}`);
        }
        
        const data = await response.json();
        
        if (data.status === 'success' && data.predictions) {
          return data.predictions;
        } else {
          throw new Error(data.error || '未获取到表情分析结果');
        }
      } catch (error) {
        console.error('表情分析出错:', error);
        throw error;
      }
    },
    
    // 分析姿态
    async analyzePose(base64Image) {
      try {
        // 捕获当前视频帧并发送到后端
        const response = await fetch(`${this.API_BASE_URL}/process_pose_frame`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ base64: base64Image })
        });
        
        if (!response.ok) {
          const errorText = await response.text();
          throw new Error(`姿态分析失败: ${response.status} - ${errorText.substring(0, 100)}`);
        }
        
        const data = await response.json();
        
        if (data.status === 'success' && data.result) {
          return {
            pose: data.result.pose,
            score: data.result.score,
            fps: data.result.fps || 0
          };
        } else {
          throw new Error(data.error || '未获取到姿态分析结果');
        }
      } catch (error) {
        console.error('姿态分析出错:', error);
        throw error;
      }
    },
    
    // 综合分析表情和姿态
    async analyzeComprehensive(index) {
      const msg = this.messages[index];
      if (!msg || msg.type !== 'expression') return;
      
      this.showStatus('正在进行综合分析...', 'info');
      
      try {
        // 构建提示信息
        let expressionsText = msg.expressionResults.map(exp => 
          `${exp.emotion} (${(exp.confidence * 100).toFixed(1)}%)`
        ).join('、');
        
        let prompt = `根据以下表情和姿态信息进行综合分析:
表情: ${expressionsText}
姿态: ${msg.poseResult.pose} (置信度: ${(msg.poseResult.score * 100).toFixed(1)}%)

请分析这个人可能的情绪状态和意图，并给出简要解读。`;
        
        const response = await fetch(`${this.API_BASE_URL}/api/chat`, {
          method: 'POST',
          headers: {
            ...this.commonHeaders,
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ prompt: prompt })
        });
        
        if (!response.ok) {
          throw new Error(`综合分析请求失败: ${response.status}`);
        }
        
        const data = await response.json();
        
        if (data.status === 'success') {
          // 更新消息对象
          this.messages[index] = {
            ...this.messages[index],
            comprehensiveAnalysis: data.result
          };
          
          // 保存综合分析到localStorage（追加方式）
          const existingAnalysis = localStorage.getItem('interview_expression_gesture_analysis') || '';
          const newAnalysis = existingAnalysis ? `${existingAnalysis}\n${data.result}` : data.result;
          localStorage.setItem('interview_expression_gesture_analysis', newAnalysis);
          
          this.showStatus('综合分析完成', 'success');
        } else {
          throw new Error(data.result || '综合分析失败');
        }
      } catch (error) {
        console.error('综合分析失败:', error);
        this.showStatus(`综合分析失败: ${error.message}`, 'error');
      }
    },
    
    // 打开评测报告弹窗
    async openEvaluationReport() {
      this.isReportVisible = true;
      this.reportLoading = true;
      this.reportError = '';
      this.reportResponse = null; // 重置响应数据
      
      // 延迟执行，确保弹窗已渲染
