<template>
    <!-- 消息界面 -->
    <div id="message" :style="{height:contentHeight}">
        <!-- 已删除搜索框 -->
        <ul class="ui-list ui-list-function ui-border-tb">
            <!-- v-for列表渲染，friendMessageList -->
            <li style="margin:0;" v-for="item in friendMessageList" :key="item.name">
                <van-swipe-cell style="width:100%;">
                    <van-cell 
                        center 
                        clickable 
                        :title="item.name" 
                        label="" 
                        :to="'/dialog/'+item.name"
                        @click="handleCellClick(item.name)"
                    >
                        <template #icon>
                            <van-image
                                round
                                width="50px"
                                height="50px"
                                :src="item.profile"
                                style="margin-right:10px;"/>
                        </template>
                        <!-- 已删除消息数量标记 -->
                    </van-cell>
                    <template #right>
                        <van-button square type="primary" text="置顶" style="height:100%;" />
                        <van-button square type="danger" text="删除" style="height:100%;" />
                    </template>
                </van-swipe-cell>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    data() {
        return {
            // 消息界面的高
            contentHeight: window.innerHeight - 100 + "px",
            // 已删除搜索框相关数据
            // 好友消息列表（保留结构，后续可根据需要调整）
            friendMessageList: [
                {name: "人工智能技术岗面试官", profile: require('../assets/images/profile_拉菲.jpg')},
                {name: "大数据运维测试岗面试官", profile: require('../assets/images/profile_02.jpg')},
                {name: "物联网产品岗面试官", profile: require('../assets/images/profile_栗山未来.jpg')}
            ]
        };
    },
    methods: {
        // 定义处理方法，在方法内部访问localStorage
        handleCellClick(name) {
            // 查找"面"字的位置
            const index = name.indexOf('面');
            // 截取需要的部分
            const career = index !== -1 ? name.substring(0, index) : name;
            // 存入localStorage
            localStorage.setItem('career', career);
        }
    }
}
</script>

